:root {
  --color-white: rgb(255, 255, 255);
  --color-black: rgb(0, 0, 0);
  --color-blue: blue;

  --shadow-vertical: 1px 2px 5px 0px rgba(0,0,0,0.30);
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  
  background-color: unset;
  outline: unset;
  border: unset;
}

a {
  color: inherit;
  text-decoration: none;
}

@font-face {
  font-family: 'montserrat';
  font-weight: bold;
  font-style: normal;
  src: url('/fonts/montserrat-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'montserrat';
  font-weight: light;
  font-style: normal;
  src: url('/fonts/montserrat-light.ttf') format('truetype');
}
