.header {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;

  display: flex;
  justify-content: center;

  box-shadow: var(--shadow-vertical);
  background-color: var(--color-white);
}

.item {
  cursor: pointer;
  position: relative;

  width: 160px;
  padding: 18px 0 0 0;

  font-size: 16px;

  @mixin active {
    content: '';
    display: block;
    position: absolute;

    top: 0;
    left: 50%;
    height: 5px;
    width: calc(100% - 30px);

    background-color: var(--color-black);
    transform: translateX(-50%);
  }

  &:hover::before {
    @include active;
  }
}
